@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&family=Montserrat:wght@900&display=swap');



:root {
    --primary-color: #5E78FC;
    --secondary-color: rgba(68, 168, 190, 1);
    --font-dark: rgb(77, 81, 86);
    --main-font-family: 'Roboto', sans-serif;
    --font-color-dark-primary: #0D0D0D;
    --font-color-dark-secondary: #262626;
    --font-color-white-primary: #E6E6E6;
    --border-radius-button: 0.8rem;
    --font-size-secondary-elements: 1.2rem;
    --font-size-primary-elements: 2.5rem;
}

* {
    margin: 0;
    box-sizing: border-box;

}

button {
    background-color: transparent;
    border: none;
    display: inline-block;
}

a {
    text-decoration: none;
}

@media (max-width:1750px) {
    :root {
        --font-size-secondary-elements: 1rem;
        --font-size-primary-elements: 2rem;
    }

}