.slider-container {
  margin-left: 8%;
  margin-right: 8%;
  height: max-content;

  position: relative;
}



.slide {
  position: fixed;
  /* Should be absolute but aparently doesnt work*/
  opacity: 0;
  transition: opacity 2s ease;
  display: grid;
  grid-template-columns: 40% 50%;
  justify-content: space-between;

}

.slide.active {
  position: relative;
  transition: opacity 2s ease;
  opacity: 1;
}

.slider-nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  z-index: 1;
  margin-left: -8%;
  margin-right: -8%;
  width: 116%;

}

.slider-prev,
.slider-next {
  padding: 8px 16px;
  background-color: #33333300;
  color: #000000;
  border: none;
  border-radius: 25%;
  font-size: 16px;
  transition: background-color 0.6s;
  font-size: 2rem;
}

.highlighted {
  background-color: rgba(0, 0, 0, 0.2);
}


.slider-prev:hover,
.slider-next:hover {
  background-color: rgba(0, 0, 0, 0.2);

}

.slide img {
  max-width: 40vw;
  max-height: 100vh;
}

.slide-description {
  display: grid;

  align-items: center;
  align-self: center;
  height: fit-content;
  gap: 2rem;
}

.slide-title {
  font-size: var(--font-size-primary-elements);
  color: var(--font-color-dark-primary);
  text-align: center;
}

.slide-text {
  font-size: var(--font-size-secondary-elements);
  color: var(--font-color-dark-secondary);
  text-align: justify;
}