@import url('https://fonts.googleapis.com/css2?family=Figtree:wght@300;400&display=swap');

:root {
    --padding-horizontal-nav-elements: 1rem;
    --margin-horizontal-nav-elements: 1rem;
    --button-border-radius: .5rem;
    --nav-bar-font-family: 'Figtree', sans-serif;
    --nav-grid-gap: 2.25rem;
    --nav-bar-height: 3.5rem;
    --nav-blur: blur(5px);
}

.main-nav {
    z-index: 3;
    position: fixed;
    top: 0;
    height: var(--nav-bar-height);
    padding-left: 8%;
    padding-right: 8%;
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: space-between;
    width: 100%;
    transition: 500ms;
}






.nav-left {
    width: 3.5rem;
}

.main-nav img {
    max-height: 2rem;
}

.main-nav>div {
    display: flex;
    height: calc(var(--nav-bar-height) * 0.8);
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    border-radius: var(--button-border-radius);

}

.main-nav .nav-center {
    display: grid;
    grid-auto-flow: column;
    gap: var(--nav-grid-gap);
}

.nav-left a:hover {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    padding-left: var(--padding-horizontal-nav-elements);
    padding-right: var(--padding-horizontal-nav-elements);
}

.nav-center a {
    padding-left: var(--padding-horizontal-nav-elements);
    padding-right: var(--padding-horizontal-nav-elements);
}

.nav-right a:hover {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    padding-left: var(--padding-horizontal-nav-elements);
    padding-right: var(--padding-horizontal-nav-elements);
}

.main-nav a {
    font-family: var(--nav-bar-font-family);
    color: var(--font-color-dark-secondary);
    font-size: 1.1rem;
    border-radius: var(--border-radius-button);
    display: flex;
    align-items: center;
}

.main-nav a:hover {
    transition: 20ms;
    background-color: rgba(0, 0, 0, 0.1);

}

.dropdown {
    position: relative;
    display: block;
}

.dropdown .dropdown-content {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-top: calc(var(--nav-bar-height) * 0.8);
    display: none;
    position: absolute;
    top: 0;
    z-index: 3;
    gap: calc(var(--nav-bar-height) * 0.2);
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 0 var(--button-border-radius) var(--button-border-radius) var(--button-border-radius);
}

.dropdown .dropdown-content>* {
    display: flex;
    height: calc(var(--nav-bar-height) * 0.8);
    position: relative;
    border-radius: var(--button-border-radius);
}



.dropdown:hover .dropdown-content {
    display: grid;
}

.main-nav .dropdown:hover {
    transition: 20ms;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: var(--button-border-radius) var(--button-border-radius) 0 0;

}




.nav-scrolled {
    background-color: rgba(255, 255, 255, 0.3) !important;

}


.nav-scrolled-hover:hover {
    background-color: rgba(255, 255, 255, 0.3) !important;
}

.nav-scrolled a:hover {
    background-color: rgba(255, 255, 255, 0.3) !important;
}

.main-nav .nav-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    height: var(--nav-bar-height);
    width: 100%;
    display: flex;
    transform: none;
    border-radius: 0;
    backdrop-filter: var(--nav-blur);
}





.dropdown .dropdown-content {
    margin-left: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-right: calc(0rem - var(--padding-horizontal-nav-elements));
    margin-top: calc(var(--nav-bar-height) * 0.8);
}