.photo-grid {
    display: grid;
    gap: .5rem;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 3rem;
    margin-left: 8%;
    margin-right: 8%;
    margin-bottom: 6rem;
}

.photo-grid img,
.photo-grid video {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.photo-grid>:hover {
    filter: brightness(1.15);
}